@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap');

:root {
  --bg-color: #ffffff;
  --text-color: #1a1a1a;
  --card-bg: #f7f7f7;
  --border-color: #e0e0e0;
  --hover-color: #f0f0f0;
  --shadow-color: rgba(0, 0, 0, 0.05);
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 2rem;
}

.header {
  text-align: center;
  margin-bottom: 4rem;
  padding: 4rem 0;
  border-bottom: 1px solid var(--border-color);
}

.name {
  font-size: 2.5rem;
  font-weight: 500;
  margin-bottom: 1rem;
  letter-spacing: -0.5px;
}

.title {
  font-size: 1.1rem;
  font-weight: 400;
  color: #666;
  max-width: 600px;
  margin: 0 auto;
}

.kanban {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.column {
  background: var(--bg-color);
  border-radius: 4px;
  padding: 1.5rem;
}

.column-header {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
  color: #666;
  font-weight: 500;
}

.card {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 1.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: var(--transition);
  overflow: hidden;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
}

.card.expanded {
  background: var(--bg-color);
  margin: 1rem -1.5rem;
  padding: 2rem;
  border-radius: 0;
  border-left: none;
  border-right: none;
}

.card-title {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.card-period {
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 1rem;
}

.card-content {
  max-height: 0;
  opacity: 0;
  transition: var(--transition);
  overflow: hidden;
}

.card.expanded .card-content {
  max-height: 1000px;
  opacity: 1;
  margin-top: 1rem;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.tag {
  font-size: 0.75rem;
  padding: 0.25rem 0.75rem;
  background: var(--bg-color);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  color: #666;
}

.contact-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--bg-color);
  padding: 1rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  border-top: 1px solid var(--border-color);
  backdrop-filter: blur(10px);
}

.contact-link {
  color: var(--text-color);
  text-decoration: none;
  font-size: 0.875rem;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.contact-link:hover {
  color: #666;
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  .header {
    padding: 2rem 0;
    margin-bottom: 2rem;
  }
  
  .name {
    font-size: 2rem;
  }
  
  .kanban {
    grid-template-columns: 1fr;
  }
  
  .card.expanded {
    margin: 1rem -1rem;
    padding: 1.5rem;
  }
}
